<template>
  <link rel="stylesheet" href="./css/pico.min.css">
  <main class="container">
    <div class="hello">
      <a @dblclick.prevent="showRawData()"><h1>Counter App</h1></a>
    </div>
      <div id="app" v-for="cntr in cntrs" :key="cntr.id">
        <div v-for="ct in cntrTypes" :key="ct.num">
          <div class="left">
            <button :id="'plus_' + cntr.id + '_' + ct.num + ''" @click.prevent="countIt($event)">+ {{ ct.num }} {{ cntr.id }}</button>
          </div>
        </div>
        <div v-for="ct in cntrTypes" :key="ct.num" style="display:none">
          <button :id="'minus_' + cntr.id + '_' + ct.num + ''" @click.prevent="countIt($event)">- {{ ct.num }} {{ cntr.id }}</button>
        </div>
        <span id="{{ cntr.id }}">{{ cntr.id }} count: <span id="{{ cntr.id }}_count">{{ cntr.amt }}</span></span>
      </div>
    <br><br>
    <br><br>
    current:
    <div v-for="(curr, index) in current" :key="index">
      {{ curr.txt }}
    </div>
    <br><br><br>
    history:
    <div v-for="(history, index) in histories" :key="index">
      {{ history.txt }}<br>
    </div>
    <br><br><br>
    <button :id="showAllData" @click.prevent="showAllData()">show all data</button>
    <br><br><br>
    <input v-if="showDisplayHistory" id="displayHistory" type="text" v-model="displayHistory" />
    <br><br><br>
    <div v-for="historyDisp in historyDisplay" :key="historyDisp.idx">
      {{ historyDisp.txt }}
    </div>
  </main>
</template>

<script>

export default {
  name: 'App',
  components: {},
  data () {
    return {
      histories: [
        {txt: '1/31/2023 rc 5 other 20 ambig 12 torn 4 kab 0'},
        {txt: '1/30/2023 rc 3 other 33 ambig 21 torn 0 kab 1'}
      ],
      current: [],
      historyDisplay: [],
      displayHistory: '',
      showDisplayHistory: false,
      selectedProfile: 'compact',
      profiles: ['full', 'compact'],
      profilesMappings: [{name: 'full', counters: [{num: 10}, {num: 7}, {num: 4}, {num: 2}, {num: 1}]},{name: 'compact', counters: [{num: 1}]}],
      counters: ['rc', 'other', 'ambig', 'torn', 'kab'],
      cntrs: [{id: 'rc', amt: 0}, {id: 'other', amt: 0}, {id: 'ambig', amt: 0}, {id: 'torn', amt: 0}, {id: 'kab', amt: 0}],
      exportResults: '',
      cntrTypes: [{num: 10}, {num: 7}, {num: 4}, {num: 2}, {num: 1}],
      rc: 0,
      other: 0,
      ambig: 0,
      torn: 0,
      kab: 0
    }
  },
  mounted () {
    this.process_counts()
    this.create_history()
    this.do_current()
  },
  methods: {

    showAllData() {
      console.log('hi')
      for (const key in localStorage) {
        const text = { txt: key + ': ' + localStorage[key] }
        this.historyDisplay.push(text)
      }
    },

    showRawData() {
      this.showDisplayHistory = true
      this.displayHistory = '['
      for (const key in localStorage) {
        this.displayHistory = this.displayHistory + '{"' + key + '":' + localStorage.getItem(key) + '},'
      }
      this.displayHistory = this.displayHistory + ']'
    },

    process_counts () {
      this.rc = 0
      this.other = 0
      this.ambig = 0
      this.torn = 0
      this.kab = 0
      this.cntrs = [{id: 'rc', amt: 0}, {id: 'other', amt: 0}, {id: 'ambig', amt: 0}, {id: 'torn', amt: 0}, {id: 'kab', amt: 0}]
      for (const x in this.profilesMappings) {
        const mapping = this.profilesMappings[x]
        if (this.selectedProfile == mapping.name) {
          this.cntrTypes = mapping.counters
        }
      }
      const today = new Date().toLocaleDateString()
      for (const key in localStorage) {
        for (const x in this.counters) {
          const counter = this.counters[x]
          if (key.includes(counter) && !key.includes('history_') && !key.includes('current_')) {
            const keyBits = key.split('-')
            const dt = keyBits[1]
            const amountToAdd = parseInt(localStorage.getItem(key))
            if (dt.includes(today)) {
              for (const y in this.cntrs) {
                if (this.cntrs[y].id === counter) {
                  this.cntrs[y].amt += amountToAdd
                }
              }
              const currentKey = 'current_' + keyBits[0] + '-' + dt
              if (localStorage[currentKey] === undefined) {
                localStorage.setItem(currentKey, 0)
              }
              localStorage.setItem(currentKey, amountToAdd)
            } else {
              const historyKey = 'history_' + keyBits[0] + '-' + dt
              if (localStorage[historyKey] === undefined) {
                localStorage.setItem(historyKey, 0)
              }
              localStorage.setItem(historyKey, amountToAdd)
            }
          }
        }
      }
    },

    do_current() {
      var doingCurrent = []
      for (const key in localStorage) {
        const currentKey = 'current_'
        if (key.includes(currentKey)) {
          var hasDayForCurrentSet = 0
          const lastBits = key.split('-')
          const whichDay = lastBits[1].split(',')
          const justTheDate = whichDay[0]
          const firstBits = lastBits[0].split('_')
          const typ = firstBits[1]
          for (const x in this.current) {
            if (this.current[x].txt.includes(justTheDate)) {
              hasDayForCurrentSet = 1
            }
          }
          if (hasDayForCurrentSet == 0 && doingCurrent[justTheDate] === undefined) {
            doingCurrent[justTheDate] = {}
          }
          for (const x in this.counters) {
            const counter = this.counters[x]
            if (doingCurrent[justTheDate][counter] === undefined) {
              doingCurrent[justTheDate][counter] = 0
            }
            if (typ === counter) {
              doingCurrent[justTheDate][counter] += parseInt(localStorage.getItem(key))
            }
          }
        }
      }
      for (const dt in doingCurrent) {
        var text = {txt: dt}
        const bits = doingCurrent[dt]
        for (const x in bits) {
          text.txt = text.txt + ' ' + x + ' ' + bits[x]
        }
        this.current.push(text)
      }
    },

    create_history() {
      var creatingHistory = []
      for (const key in localStorage) {
        const historyKey = 'history_'
        if (key.includes(historyKey)) {
          var hasDayForHistorySet = 0
          const lastBits = key.split('-')
          const whichDay = lastBits[1].split(',')
          const justTheDate = whichDay[0]
          const firstBits = lastBits[0].split('_')
          const typ = firstBits[1]
          for (const x in this.histories) {
            if (this.histories[x].txt.includes(justTheDate)) {
              hasDayForHistorySet = 1
            }
          }
          if (hasDayForHistorySet == 0 && creatingHistory[justTheDate] === undefined) {
            creatingHistory[justTheDate] = {}
          }
          for (const x in this.counters) {
            const counter = this.counters[x]
            if (creatingHistory[justTheDate][counter] === undefined) {
              creatingHistory[justTheDate][counter] = 0
            }
            if (typ === counter) {
              creatingHistory[justTheDate][counter] += parseInt(localStorage.getItem(key))
            }
          }
        }
      }
      for (const dt in creatingHistory) {
        var text = {txt: dt}
        const bits = creatingHistory[dt]
        for (const x in bits) {
          text.txt = text.txt + ' ' + x + ' ' + bits[x]
        }
        this.histories.push(text)
      }
    },

    export () {
      this.exportResults = '['
      for (const key in localStorage) {
        this.exportResults = this.exportResults + '{"' + key + '":' + localStorage.getItem(key) + '},'
      }
      this.exportResults = this.exportResults + ']'

    },

    countIt (event) {
      const data = event.target.id.split('_')
      const typ = data[0]
      const which = data[1]
      const amount = data[2]
      const now = new Date().toLocaleDateString()
      const otherPart = Date.now()
      const key = which + '-' + now + '-' + otherPart
      let toSaveDown = 0
      if (typ === 'plus') {
        toSaveDown += amount
      } else if (typ === 'minus') {
        toSaveDown -= amount
      }
      localStorage.setItem(key, toSaveDown)
      this.process_counts()
    }

  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.left {
  float: left;
}

</style>
